
@mixin sub-small-title {
    font: {
        size: $font-size-base;
        weight: 500;
    }
    user-select: none;
    color: $gray-500;
}

@mixin fit-image {
    img {
        height: 100%;
        width: 100%;
    }
}

@mixin loading-element {
    background: $loading-background;
    background-size: 300% 300%;
    animation: ElementOnLoading 2s ease infinite;
}

@mixin loading-with-spinner {
    animation: LoadingWithSpinner 1s ease-in-out infinite;
}

@mixin no-wrap-text($overflow: "ellipsis") {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: #{$overflow};
}

@mixin no-outline-focus {
    &:focus {
        outline: none;
        box-shadow: none;
    }
}

@mixin public-form {
    text-align: center;
    margin-bottom: 10%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &__container {
        width: $public-form-default-width;
    }
    h1 {
        font: {
            size: $public-form-h1-size;
            weight: $public-form-h1-weight;
        }
        font-family: "Montserrat", sans-serif;
        margin-bottom: 60px;
    }
    h2 {
        font: {
            size: $public-form-h2-size;
            weight: $public-form-h2-weight;
        };
        margin-bottom: 10px;
    }
    .form-group {
        margin-bottom: 30px;
    }
    .form-control {
        font-size: $font-size-base * 1.143;
    }
    &__submit {
        margin-top: 15px;
        width: 100%;
    }
}

@mixin page {
    padding: 25px 40px;
    &__header {
        margin-bottom: 40px;
    }
    &__title {
        font-size: $page-title-font-size;
        user-select: none;
    }
    &__body {
        padding: 0 15px;
    }
}

@mixin page-layout-sidebar() {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-height: 0;

    &__content {
        padding: 0 15px;
        overflow: auto;
        flex-grow: 1;
        min-height: 0;
    }
    &__footer {
        padding: 15px 15px 0 15px;
    }
}

@mixin enable-focus-within {
    &:focus-within {
        color: $input-focus-color;
        background-color: $input-focus-bg;
        border-color: $input-focus-border-color;
        outline: 0;
        @if $enable-shadows {
            box-shadow: $input-box-shadow, $input-focus-box-shadow;
        } @else {
            box-shadow: $input-focus-box-shadow;
        }
    }
    &.is-invalid:focus-within {
        border-color: $danger;
        box-shadow: 0 0 0 $input-focus-width rgba($danger, .25);
        width: 100%;
    }
}

@mixin img-size($value) {
    width: $value;
    height: $value;
}

@mixin text-muted {
    font: {
        size: $font-size-base * 0.875;
        weight: 400;
    }
    color: $text-muted;
}

@mixin section-template {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $page-block-bg-color;
    &__content {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__text {

    }
}