@import "../../styles/common";

$login-form-captcha-height: 51px;

.login-form {
    @include public-form;
    &__login.form-group {
        margin-bottom: 10px;
    }
    &__captcha {
        img {
            width: 100%;
            height: $login-form-captcha-height;
            border: 1px solid $gray-400;
        }
    }
    &__captcha-input {
        .rich-horizontal-input__description {
            padding-right: 1px;
        }
        .rich-horizontal-input__field {
            padding-left: 1px;
            .form-control {
                height: 100%;
            }
        }
    }
    &__error-container {
        margin-top: 20px;
    }
    &__error {
        color: $danger
    }
    &__submit {
        border: none;
    }
}