@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

@font-face {
  font-family: 'Lato';
  src: url("../assets/fonts/Lato-Bold.ttf");
  src: url("../assets/fonts/Lato-Bold.eot") format("embedded-opentype"),
  url("../assets/fonts/Lato-Bold.woff") format("woff"),
  url("../assets/fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Lato';
  src: url("../assets/fonts/Lato-Regular.ttf");
  src: url("../assets/fonts/Lato-Regular.eot") format("embedded-opentype"),
  url("../assets/fonts/Lato-Regular.woff") format("woff"),
  url("../assets/fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Lato';
  src: url("../assets/fonts/Lato-Light.ttf");
  src: url("../assets/fonts/Lato-Light.eot") format("embedded-opentype"),
  url("../assets/fonts/Lato-Light.woff") format("woff"),
  url("../assets/fonts/Lato-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

$font-family-sans-serif: 'Lato', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
