@import "../../styles/common";

$pubic-page-body-margin-top: 20vh;

.public-page {
    overflow-y: hidden;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: {
        left: 15px;
        right: 15px;
        top: 40px;
    };
    &__header {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-self: flex-start;
        .header__left {

        }
        .header__right {
            margin-left: auto;
        }
    }
    &__body {
        width: 100%;
        justify-self: center;
        margin: {
            top: $pubic-page-body-margin-top;
            bottom: 30px;
        };
    }
    &__footer {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-self: flex-end;
        margin-top: auto;
        .footer__left {

        }
        .footer__right {
            margin-left: auto;
        }
    }
    &-message {
        text-align: center;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &__icon {

        }
        &__text {
            font: {
                size: $public-form-h2-size;
                weight: $public-form-h2-weight;
            };
        }
    }
}