@import "../../styles/common";

$error-page-title-size: $font-size-base * 2;
$error-page-text-size: $font-size-base * 1.2;

.error-page {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $im-public-bg;
  color: $im-public-color;
  &__title {
    font: {
      size: $error-page-title-size;
      weight: 300;
    };
    margin-bottom: 20px;
  }
  &__text {
    font: {
      size: $error-page-text-size;
      weight: 300;
    }
  }
}